h1, h2, h3, h4, .ui.header, 
.ui.sub.header {
    font-family: 'Raleway', sans-serif;
}
hr {
    margin-top: 25px;
}
html, body {
    height:100%;
    margin: 0;
}
.portfolio-body p {
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 1px;
}
.welcome-home {
    padding: 45px 0px;
}
.portfolio-title .portfolio-header {
    padding: 5em .8em 3em .8em;
    letter-spacing: 2px;
    font-weight: 800;
}

.portfolio-header h4.sub.header {
    color: rgba(0,0,0, 0.5);
    letter-spacing: 2px;
    margin-top: 5px;
}

.jump-top {
    color:rgb(242, 124, 74);
    text-align: right;
    padding: 10px;
    margin-bottom: 15px;
}
.jump-top:hover {
    color:rgb(212, 69, 0);
}
.ui.theme-orange.button {
    background-color:rgb(242, 124, 74);
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    letter-spacing: 1px;
}
button a {
    text-decoration: none;
}

.ui.theme-orange.button:hover {
    background-color:rgb(212, 69, 0);
}

.ui.secondary.menu .item, .linkedin {
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 15px;
    font-size: 14px;
}

.footer {
    background-image: linear-gradient( 111deg, rgba(44,204,176,0.5) 78%, rgba(255,255,255,1) 75% );
    height: 245px;
    position: relative; 
    left: 0;
    right: 0; 
    bottom: 0; 

}
.portfolio-title {
    background-image: linear-gradient( 111deg,  
    rgba(255,255,255,1) 78%, rgba(44,204,176,0.5) 75% );
}

.project-link {
    text-decoration: none;
    color: white;
}
.project-link:hover {
    text-decoration: none;
    color: white;
    background-color:rgb(212, 69, 0);
}


.image {
    height: 200px;
    border-radius: 50%;
    margin-bottom: 30px;
}
.project-select {
    padding-top: 15px;
}
.project-details {
    padding-bottom: 250px;
}

.project-body, .project-details {
    padding-top: 50px;
}

@media only screen and (max-width: 769px) {  
    .portfolio-title .portfolio-header{
        padding: 1.5em .3em 0.5em  .3em;
    }
}

@media only screen and (max-width: 525px) {  
    .portfolio-title .portfolio-header{
        padding: 1.5em .3em 0.5em  .3em;
    }
}